









import Vue from 'vue';
export default Vue.extend({
	props: {
		tab: {
			type: Number,
			default: 0
		}
	},
	data() {
		return { tabs: null };
	},
	watch: {
		tab(newValue): void {
			this.tabs = newValue;
		},
		tabs(newValue: string): void {
			this.$emit('update:tabValue', newValue);
		}
	},
	mounted() {
		this.tabs = this.tab;
	}
});
